*{
    padding: 0;
    margin: 0;
    line-height: 1;
    text-decoration: none;
    box-sizing: border-box;
}
header{
    position: fixed;
    padding: 30px 0 30px 40px;
}
nav{
    a{
        color: rgba($color: #fff, $alpha: 0.6);
        font-size: 16px;
        margin-right: 20px;
        font-family: 'Open Sans',sans-serif;
        display: inline-block;
    }
}
main{
    height: 100vh;
}

.hero{
    font-size: 0;
    .intro{
        width: 50%;
        background-color: #010101; 
        display: inline-block;
        height: 100vh;
        .intro-content{
            position: absolute;
            top:50%;
            left: 25%;
            transform: translate(-50%, -25%);
        }
    }
    .profile-image{
        background: url(../images/pooja-photo.jpg) no-repeat;
        height: 100vh;
        width: 50%;
        background-size: 100%;
        display: inline-block;
    }
    h1{
        font-size: 56px;
        color: #fff;
        margin-bottom: 15px;
        font-family: 'Ubuntu',sans-serif;
        letter-spacing: 14;
        font-weight: 500;
        letter-spacing: 1px;
    }
    h3{
        font-size: 22px;
        color: rgba($color: #fff, $alpha: 0.6);
        font-family: 'Open Sans',sans-serif;
        font-weight: 400;
        text-align: center;
        letter-spacing: 1px;
    }
    button{
        padding: 14px 20px;
        border-radius: 100px;
        border: 1px solid #fff;
        background: #fff;
        margin-top: 40px;
        margin-left: calc(50% - 100px);
        &:hover{
            background-color: transparent;
            transition: 0.5s all ease-in-out;
            a{
                color: #fff;
            }
        }
        a{
            color: #010101;
            font-size: 18px;
            font-family: 'Open Sans',sans-serif;
        }
    }
}

.container{
    width: 1080px;
    margin: 0 auto;
}